@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);

section#logInSection {
    max-width: 500px;
    box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 0.4);
    padding: 25px;
    border-radius: 10px;
    margin: 3rem auto;
}

div.alert {
    max-width: 500px;
    margin: 1rem auto;
}

div.timer-wrapper {
    display: flex;
    justify-content: center;
  }

  div.timer-wrapper div{
    margin:auto;
  }
  
  div.timer-wrapper .timer {
    font-family: "Montserrat";
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  div.timer-wrapper .text {
    color: #aaa;
  }
  
  div.timer-wrapper .value {
    font-size: 40px;
  }
  
  div.timer-wrapper .info {
    max-width: 360px;
    margin: 40px auto 0;
    text-align: center;
    font-size: 16px;
  }
  div.timer-wrapper svg{
    background: white;
    border-radius: 100%;
  }
.pro-sidebar-header, .pro-sidebar-footer{
    text-align: center;
}

.switch-collapsed{
    /*display: none;*/
}

.sidebar-btn{
    cursor: pointer;
}
