@import "../../Assets/variables.scss";

.MuiDrawer-paper{
    width: $sidebar-disappearing-width;
}
#barHeader{
    height: $navbar-height;
    line-height: $line-height;
    .barHeaderContent{
        width: 100%;
        height: fit-content;
        margin: auto auto;
    }
}
#barContent{
    max-width: $sidebar-disappearing-width;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
}
.defaultIcon {
    color: $main-background-color;
    margin: auto auto;
    display: inline;
    font-size: $icon-size;
}
